export default {
	data() {
		return {
			isShowNoList: false,
			harvest_id: null,
			harvestInfo: null,
			from_page: 1,
			from_page_function: 1,
			isCaidi: 0,
			address_type: 0,
			vegetablefield_id: '',

			available_list: '',
			unavailable_list: ''
		}
	},
	onLoad: function(options) {
		//跳转的页面from_page  此页面地址功能 from_page_function 
		//from_page 1表示我的鸡 
		//from_page_function 1表示送人
		var from_page = options.from_page;
		var from_page_function = options.from_page_function;
		var address_type = options.address_type;
		this.address_type = address_type;
		this.from_page = from_page;
		this.from_page_function = from_page_function;
		//菜地初始化数据
		var harvestInfo = options.harvestInfo;
		this.isCaidi = options.isCaidi || null;
		this.harvest_id = options.harvest_id || null;
		if (harvestInfo != null) {
			harvestInfo = JSON.parse(harvestInfo);
		}
		this.harvestInfo = harvestInfo;
		this.vegetablefield_id = options.vegetablefield_id;
		this.period_number_id = options.period_number_id;
		this.address_lists();
	},
	methods: {
		address_lists() {
			var that = this;
			var param = {
				user_id: this.globalData.user_info.userid,
				vegetablefield_id: this.vegetablefield_id,
				period_number_id: this.period_number_id,
				pagenum: 50
			}

			this.xm_config.address_lists_baoyou(param)
				.then(function(res) {
					if (res.data.status == 1) {
						if (res.data.info != null) {
							if (res.data.info.available_list != null) {
								that.available_list = res.data.info.available_list;
								that.isShowNoList = res.data.info.available_list.length > 0 ? false : true
							}
							if (res.data.info.unavailable_list != null) {

								that.unavailable_list = res.data.info.unavailable_list;
								that.isShowNoList = res.data.info.unavailable_list.length > 0 ? false : true;
							}

							if (res.data.info.available_list != null && res.data.info.unavailable_list != null) {
								that.isShowNoList = true;
							} else {
								that.isShowNoList = false;
							}

						} else {
							uni.showToast({
								title: res.data.msg,
							})
							that.isShowNoList = true;
						}


					} else {
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						})
						this.isShowNoList = true;
					}
				});
		},

		/**
		 * 选择地址
		 */
		onSelectAddress: function(item) {
			var that = this;
			if (this.address_type == 0) {
				this.active = item.id;
				
				var pages = getCurrentPages();
				var currPage = pages[pages.length - 1]; //当前页面
				var prevPage = pages[pages.length - 2]; //上一个页面

				//我的鸡页面
				if (this.from_page == 1) {
					item.from_page = this.from_page
					item.from_page_function = this.from_page_function;
					if (item.from_page_function == 1) {
						uni.showModal({
							title: '温馨提示',
							content: "地址修改将从下次未进入发货流程的记录开始生效",
							showCancel: true,
							complete: function(res) {
								if (res.confirm) {
									prevPage.$vm.setAddressInfo(item);
									setTimeout(function() {
										uni.navigateBack({

										})
									}, 200) //延迟时间 这里是1秒
								} else {
									that.active = 0
								}
							}
						})
					} else if (item.from_page_function == 2) {
						uni.showModal({
							title: '温馨提示',
							content: "请您核对地址信息本次收蛋送人后将不可修改",
							showCancel: true,
							complete: function(res) {
								if (res.confirm) {
									prevPage.$vm.setAddressInfo(item);
									setTimeout(function() {
										uni.navigateBack({

										})
									}, 200) //延迟时间 这里是1秒
								} else {
									that.active = 0
								}
							}
						})
					}
				}
			}
			if (this.address_type == 1) {
				var pages = getCurrentPages();
				var currPage = pages[pages.length - 1]; //当前页面
				var prevPage = pages[pages.length - 2]; //上一个页面
				//2、判断是否为送人
				//3、判断是否已经进入收菜时间
				this.active = item.id
				//console.log(this.data);
				if (this.harvestInfo != null && this.harvestInfo.is_flow == 1) {
					var that = this;
					uni.showModal({
						title: '温馨提示',
						content: "地址修改将从下次未进入发货流程的记录开始生效",
						showCancel: true,
						complete: function(res) {
							if (res.confirm) {
								prevPage.$vm.setAddressInfo(item);
								setTimeout(function() {
									uni.navigateBack({

									})
								}, 200) //延迟时间 这里是1秒
							} else {
								that.active = 0;
							}
						}
					})
				} else if (this.harvestInfo != null && this.harvestInfo.is_flow == 0) {
					var that = this;
					uni.showModal({
						title: '温馨提示',
						content: "更换默认收菜地址后此菜地将按照新地址进行派送？",
						showCancel: true,
						complete: function(res) {
							if (res.confirm) {
								prevPage.$vm.setAddressInfo(item);
								setTimeout(function() {
									uni.navigateBack({

									})
								}, 200) //延迟时间 这里是1秒
							} else {
								that.active = 0
							}
						}
					})
				}

				if (this.harvest_id != null) {
					var that = this;
					uni.showModal({
						title: '温馨提示',
						content: '请您核对地址信息本次收菜送人后将不可修改',
						showCancel: true,
						complete: function(res) {
							if (res.confirm) {
								prevPage.$vm.setSendAddressInfo(item);
								setTimeout(function() {
									uni.navigateBack({

									})
								}, 200) //延迟时间 这里是1秒
							} else {
								that.active = 0
							}
						}
					})
					return
				}

				if (this.harvestInfo == null && this.harvest_id == null) {
					prevPage.$vm.setAddressInfo(item);
					setTimeout(function() {
						wx.navigateBack({

						})
					}, 200) //延迟时间 这里是1秒
				}
			}
		},
		
		onEditAddressClick: function(item) {
			var address_info = item;
			uni.navigateTo({
				url: '../address-add/address-add?address=' + JSON.stringify(address_info),
			})
		},
		
		onDeleteAddressClick: function(item) {
			var address_info = item;
			var that = this
			uni.showModal({
				title: '删除地址',
				content: '确定删除吗？',
				showCancel: true,
				success: function(res) {
					if (res.confirm) {
						that.del_user_address(address_info.id);
					}
				}
			})
		},
		
		del_user_address: function(id) {
			var that = this;
			var param = {
				id: id
			}
			this.xm_config.del_user_address(param)
				.then(function(res) {
					if (res.data.status == 1) {
						uni.showToast({
							title: '删除成功',
							icon: "none"
						})
						that.address_lists();
		
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						})
					}
				});
		},
		
		onAddAddressClick: function() {
			uni.navigateTo({
				url: '../address-add/address-add',
			})
		},
	}
}
